import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Route } from "react-router-dom";
import axios from 'axios';
import {inject} from 'mobx-react';

import AnimatedSwitch from '../components/animated-switch';
import confirmAction from '../utils/confirmAction';

import TimetableDaySchedule from './TimetableDaySchedule';
import TimetableLabs from './TimetableLabs';

export default class TimetablePages extends Component {
    render() {
        let match = this.props.match;
        return <AnimatedSwitch>
            <Route path={`${match.path}/day-schedule`} render={(props) => <TimetableDaySchedule {...props} /> } />
            <Route path={`${match.path}/labs`} render={(props) => <TimetableLabs {...props} /> } />
            <Route exact path={`${match.path}`} render={(props) => <Timetable {...props} /> } />
        </AnimatedSwitch>
    }
}

@inject('classesStore')
class Timetable extends Component {
	state = {payload: '', streams: [], classes: []}

	componentDidMount() {
		this.props.classesStore.list({}, true).then(classes=>{
			const streams = classes.reduce( (streams, cls) => {streams = streams.concat(cls.streams); return streams},[]);
			this.setState({classes, streams});
		})

		axios.get('/api/timetable/allocations').then(res => {
			this.setState({allocations: res.data});
		});

		axios.get('/api/timetable').then(res => {
			this.setState({timetable: res.data});
		});
	}

	filterChanged = (e) => {
		let field = e.target.name;
		let value = e.target.value;
		if(field == 'class_id'){
			this.setState({class_id: value, stream_id: ''})
		}else {
			this.setState({[field]: value});
		}
	}

	generate = () => {
		confirmAction( () => {
			return axios.post('/api/timetable/generate').then(res=>{
				this.setState({timetable: res.data});
				return {success: 'Generated!', succes_text: 'Timetable was generated'};
			}, error => {
				throw {error: "Timetable couldn't be generated."};
			})
		}, {loading_title: 'Generating...', loading_text: 'Generating Timetable', canceled: "Generating Timetable Operation Cancelled", consequence: 'Current Timetable will be over-written'});
	}

	render() {
		const timetable = this.state.timetable || {};
		const classes = this.state.classes;
		let filterStreams = null;
		if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        filterStreams = selectedClass.streams;
      }
    }

    //filter stream based on set class_id and stream_id
    let streams = this.state.streams.filter( (stream) => {
    	let status = true;
    	if(this.state.class_id) {
    		status = stream.class_id == this.state.class_id;
    	}
    	if(this.state.stream_id) {
    		status = stream.id == this.state.stream_id;
    	}
    	return status;
    });

		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <button onClick={this.generate} type="button" className="btn btn-primary">Generate</button>
        	      </span>
        	    </div>
    	    </div>
    	    <div className="row">
    	    	<div className="card col-12">
				      <div className="card-header text-center">
				        <h3>Timetable</h3>
				      </div>
				      <div className="card-body">
				      	<div className="row">
				      		<select className="col-sm-6 form-control" value={this.state.class_id} onChange={this.filterChanged} name='class_id' >
				      			<option value=""> -- Filter Class -- </option>
				      			{this.state.classes.map( cls => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
				      		</select>
				      		<select className="col-sm-6 form-control" value={this.state.stream_id} onChange={this.filterChanged} name='stream_id' >
				      			<option value=""> -- Filter Stream -- </option>
				      			{filterStreams && filterStreams.map( stream => <option key={stream.id} value={stream.id}>{stream.name}</option> )}
				      		</select>
				      	</div>
				      </div>
				    </div>
    	    </div>
    	    {streams.map( stream => <div key={stream.id} className="row">
    	    	<div className="col-12">
	    	    	<TimetableStream stream={stream} cls={this.state.classes.find(cls=>cls.id == stream.class_id)} table={this.state.timetable} allocations={this.state.allocations} />
	    	    </div>
    	    </div> )}
    	  </div>
	}
}


class TimetableStream extends Component {

	allocationDetail(id) {
		const allocations = this.props.allocations || [];
		const alloc = allocations.find( a => a.id == id );
		if(!alloc)
			return '-';
		return <>{alloc.subject.name}<br/>{alloc.teacher.name}</>;
		return '-';
	}
	getCellDetails(row,column) {
		const streamId = this.props.stream.id;
		if(!streamId || !this.props.table || !this.props.table.table)
			return '-'
		const streamTable = this.props.table.table[streamId];
		if(!streamTable)
			return '-';
		const allocationId = streamTable[row+1][column+1];
		if(this.props.allocations && allocationId){
			const alloc = this.props.allocations.find ( a => a.id == allocationId);
			return <>{alloc.subject.name}<br/>{alloc.teacher.name}</>;
		}
		return '-';
	}
	render() {
		const schedule = this.props.table && this.props.table.schedule ? this.props.table.schedule : [];
		const days = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday'];
		const cls = this.props.cls;
		return <div className="row">
			<div className="card col-12">
	      <div className="card-header text-center">
	        <h3>{cls && cls.name} {this.props.stream.name}</h3>
	      </div>
	      <div className="card-body">
	      	<table className="table table-striped">
	      		<thead>
	      			<tr>
	      				<th></th>
	      				{schedule.map( (slot,index) => <th key={index} >{slot.start}-{slot.end}</th> )}
	      			</tr>
	      		</thead>
	      		<tbody>
	      			{days.map( (day,dayIndex) => <tr key={dayIndex} >
	      					<th>{day}</th>
	      					{schedule.map( (slot,slotIndex) => <td key={slotIndex} >{ slot.activity == 1 ? this.getCellDetails(dayIndex, slotIndex) : <span className='badge badge-info'>Break</span>} </td> )}
	      				</tr> )}
	      		</tbody>
	      	</table>
	      </div>
	    </div>
		</div>
	}
}