import React, {Component} from 'react';
import axios from 'axios';
import {inject} from 'mobx-react';

import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';

import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  root: {
    '& > *': {
      margin: theme.spacing(1),
      width: 200,
    },
  }
}));

function LabName(props) {
	let classes = useStyles();
	return <FormControl className={classes.root} error={props.error !== ''} >
		<TextField value={props.value} onChange={props.onChange} label="Lab Name" error={props.error !== ''} helperText={props.error} />
	</FormControl>
}

function SubjectSelect(props) {
	let classes = useStyles();
	const [value, setValue] = React.useState('')
	const subjects =  props.subjects || [];
	return <FormControl className={classes.formControl} error={props.error !== ''} >
    <InputLabel id="activity-select-label">Activity</InputLabel>
    <Select
      labelId="activity-select-label"
      id="activity-select"
      value={props.value}
      onChange={props.onChange}
      autoWidth
    >
      {subjects.map( (subject) => <MenuItem key={subject.id} value={subject.id}>{subject.name}</MenuItem> )}
    </Select>
    <FormHelperText>{props.error}</FormHelperText>
  </FormControl>
}

@inject('subjectStore')
export default class TimetableDaySchedule extends Component {
	state = {subjects: [], name_add: '', subject_add: '', name_add_error: '', subject_add_error: '', labs: [] }

	componentDidMount() {
		axios.get('/api/timetable/setup').then(res=>this.setState({ labs: res.data.labs  || [] }));
		this.props.subjectStore.list({}, true).then(subjects=>{
			this.setState({subjects})
		});
	}

	save = () => {
		const labs = this.state.labs || [];
		axios.post('/api/timetable/setup', {labs}).then(res=>{
			this.setState({labs: res.data.labs});
		});
	}

	add = () => {
		let labs = this.state.labs.slice(0);
		let name_add_error = '', subject_add_error = '';
		
		if(this.state.name_add === '') {
			name_add_error = 'Name required';
		}
		if(this.state.subject_add === '') {
			subject_add_error = 'Subject required';
		}
		if(name_add_error !== '' || subject_add_error !== '' ) {
			this.setState({name_add_error, subject_add_error});
		}
		else {
			labs.push({name: this.state.name_add, subject_id: this.state.subject_add});
			this.setState({labs, name_add: '', subject_add: '', name_add_error, subject_add_error}, this.save);
		}
	}

	subjectChange = (e) => {
		this.setState({subject_add: e.target.value});
	}

	nameChange = (e) => {
		this.setState({name_add: e.target.value});
	}

	delete(index) {
		let labs = this.state.labs.slice(0);
		labs.splice(index,1);
		this.setState({labs}, this.save);
	}

	getSubjectName(subjectId) {
		let subject = this.state.subjects.find( s => s.id == subjectId);
		return subject ? subject.name : subjectId;
	}

	render() {
		let lessonCount = 0;
		let breakCount = 0;
		return <div className="container-fluid">
    	<div className="row">
    		<div className="col-sm-12">
					<div className="card">
			      <div className="card-header">
			      	<nav className="text-center">
			        	<h5>Labs</h5>
			      	</nav>
			      </div>
			      <div className="card-body table-responsive text-center">
			      	<MuiPickersUtilsProvider utils={DateFnsUtils}>
				      	<table className="table table-striped">
				      		<thead>
				      			<tr>
				      				<th>Subjct</th>
				      				<th>Name</th>
				      			</tr>
				      		</thead>
				      		<tbody>
				      			{this.state.labs.map( (value,index) => <tr key={index}>
				      				<td><button type="button" className="btn btn-info"> {this.getSubjectName(value.subject_id)}</button></td>
				      				<td><button type="button" className="btn btn-primary">{value.name}</button></td>
				      				<td><button onClick={this.delete.bind(this,index)} type="button" className="btn btn-danger"> Remove <i className="fa fa-trash"></i></button></td>
				      			</tr>)}
				      		</tbody>
				      		<tfoot>
				      			<tr>
					      			<td>
					      				<SubjectSelect subjects={this.state.subjects} onChange={this.subjectChange} value={this.state.subject_add} error={this.state.subject_add_error} />
					      			</td>
					      			<td>
					      				<LabName value={this.state.name_add} onChange={this.nameChange} error={this.state.name_add_error} />
								      </td>
					      			<td><button onClick={this.add} type="button" className="btn btn-success"> Add <i className="fa fa-plus"></i></button></td>
					      		</tr>
				      		</tfoot>
				      	</table>
			      	</MuiPickersUtilsProvider>
			      </div>
			    </div>
    		</div>
    	</div>
    </div>
	}
}
