import React, {Component} from 'react';
import axios from 'axios';

import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';

import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  }
}));

function ActivitySelect(props) {
	let classes = useStyles();
	const [value, setValue] = React.useState('')
	return <FormControl className={classes.formControl} error={props.error !== ''} >
    <InputLabel id="activity-select-label">Activity</InputLabel>
    <Select
      labelId="activity-select-label"
      id="activity-select"
      value={props.value}
      onChange={props.onChange}
      autoWidth
    >
      <MenuItem value={1}>Lesson</MenuItem>
      <MenuItem value={0}>Break</MenuItem>
    </Select>
    <FormHelperText>{props.error}</FormHelperText>
  </FormControl>
}

export default class TimetableDaySchedule extends Component {
	state = {activity_add: '', start_add: '08:00', end_add: '08:00', start_add_error: '', end_add_error: '', activity_add_error: '', schedule: [] }

	componentDidMount() {
		axios.get('/api/timetable/setup').then(res=>this.setState({ schedule: res.data.schedule || [] }));
	}

	save = () => {
		const schedule = this.state.schedule || [];
		axios.post('/api/timetable/setup', {schedule}).then(res=>{
			this.setState({schedule: res.data.schedule});
		});
	}

	add = () => {
		let schedule = this.state.schedule.slice(0);
		let lastPeriod = schedule[schedule.length - 1];
		let start_add_error = '', end_add_error = '', activity_add_error= '';
		
		if(lastPeriod && (Date.parse('2020-02-02T'+lastPeriod.end) > Date.parse('2020-02-02T'+this.state.start_add))) {
			start_add_error = 'Start should be greater than previous period end';
		}
		if(Date.parse('2020-02-02T'+this.state.start_add) >= Date.parse('2020-02-02T'+this.state.end_add)) {
			end_add_error = 'End should be greater than start of the period';
		}
		if(this.state.activity_add === '') {
			activity_add_error = 'Please set the activity';
		}

		if(start_add_error !== '' || end_add_error !== '' || activity_add_error !== '') {
			this.setState({start_add_error, end_add_error, activity_add_error});
		}
		else {
			schedule.push({activity: this.state.activity_add, start: this.state.start_add, end: this.state.end_add});
			this.setState({schedule, start_add_error, end_add_error, activity_add_error, start_add: this.state.end_add}, this.save);
		}
	}

	handleDateChangeAdd =(field, e)=> {
		if(e){
			let time = e.toTimeString().split(' ')[0].split(':').slice(0,2).join(':');
			this.setState({[field]: time});
		}
	}

	activityChange = (e) => {
		this.setState({activity_add: e.target.value});
	}

	delete(index) {
		let schedule = this.state.schedule.slice(0);
		schedule.splice(index,1);
		this.setState({schedule}, this.save);
	}

	render() {
		let lessonCount = 0;
		let breakCount = 0;
		return <div className="container-fluid">
    	<div className="row">
    		<div className="col-sm-12">
					<div className="card">
			      <div className="card-header">
			      	<nav className="text-center">
			        	<h5>Daily Schedule</h5>
			      	</nav>
			      </div>
			      <div className="card-body table-responsive text-center">
			      	<MuiPickersUtilsProvider utils={DateFnsUtils}>
				      	<table className="table table-striped">
				      		<thead>
				      			<tr>
				      				<th>Activity</th>
				      				<th>Start</th>
				      				<th>End</th>
				      				<th>Action</th>
				      			</tr>
				      		</thead>
				      		<tbody>
				      			{this.state.schedule.map( (value,index) => <tr key={index}>
				      				<td><button type="button" className="btn btn-info">{value.activity == 1 ? 'Lesson' : 'Break'} <span className="badge badge-light">{value.activity == 1 ? ++lessonCount : ++breakCount}</span></button></td>
				      				<td><button type="button" className="btn btn-primary">{value.start}<i className="fa fa-clock-o"></i></button></td>
				      				<td><button type="button" className="btn btn-success">{value.end}<i className="fa fa-clock-o"></i></button></td>
				      				<td><button onClick={this.delete.bind(this,index)} type="button" className="btn btn-danger"> Remove <i className="fa fa-trash"></i></button></td>
				      			</tr>)}
				      		</tbody>
				      		<tfoot>
				      			<tr>
					      			<td>
					      				<ActivitySelect onChange={this.activityChange} value={this.state.activity_add} error={this.state.activity_add_error} />
					      			</td>
					      			<td>
					      				<KeyboardTimePicker
								          margin="normal"
								          id="time-picker"
								          label="Start Time"
								          error={this.state.start_add_error !== ''}
								          helperText={this.state.start_add_error}
								          value={'2020-12-30T' + this.state.start_add}
								          onChange={this.handleDateChangeAdd.bind(this, 'start_add')}
								          KeyboardButtonProps={{
								            'aria-label': 'change time',
								          }} />
								      </td>
					      			<td>
					      				<KeyboardTimePicker
								          margin="normal"
								          id="time-picker"
								          label="End Time"
								          error={this.state.end_add_error !== ''}
								          helperText={this.state.end_add_error}
								          value={'2020-12-30T' + this.state.end_add}
								          onChange={this.handleDateChangeAdd.bind(this,'end_add')}
								          KeyboardButtonProps={{
								            'aria-label': 'change time',
								          }} />
								      </td>
					      			<td><button onClick={this.add} type="button" className="btn btn-success"> Add <i className="fa fa-plus"></i></button></td>
					      		</tr>
				      		</tfoot>
				      	</table>
			      	</MuiPickersUtilsProvider>
			      </div>
			    </div>
    		</div>
    	</div>
    </div>
	}
}
